<template>
  <v-row class ="pb-8">
    <v-col cols="12" sm="12" md="12">
      <v-card>
        <v-treeview
          v-if="isData"
          open-all
          dense
          rounded
          hoverable
          open-on-click
          :items="showData"
        >
          <template v-slot:prepend="{ item }">
            <v-icon
              :color= "`${item.diff === 'same' ? 'green' : item.diff === 'diff' ? 'orange' : item.diff === 'plus' ? 'red' : 'red'}`"
              v-text="`mdi-${item.diff === 'same' ? 'check' : item.diff === 'diff' ? 'not-equal-variant' : item.diff === 'plus' ? 'plus' : 'minus'}`"
            />
          </template>
        </v-treeview>
        <v-subheader v-if="!isData" class="d-flex align-center justify-center"> No data to show </v-subheader>
      </v-card>
    </v-col>
  </v-row>
</template>
<script>
  export default {
    name: 'CompareTree',

    data: () => ({
      //
    }),

    props:["inputTreeData"],

    computed: {
      showData () {
        return this.inputTreeData
      },
      isData () {
        return (this.inputTreeData.length > 0) ? "true" : false
      },
    },

    mounted () {
      //
    },
    watch: {
      //
    },
    methods: {
      //            
    }


  }
</script>