import http from "../http-common";

class entLiveParams {
  getAll(profile) {return http.get(`zookeeper/entParam/${profile}`);}
}

class entLiveCDParams {
  getAll(profile) {return http.get(`zookeeper/entComp/${profile}`);}
}

class entLiveNSSParams {
  getAll(profile) {return http.get(`zookeeper/nss/${profile}`);}
}

class serLiveParams {
  getAll(profile) {return http.get(`zookeeper/serParam/${profile}`);}
}

class serLiveCDParams {
  getAll(profile) {return http.get(`zookeeper/serComp/${profile}`);}
}

class eventLiveLog {
  getAll(profile) {return http.get(`zookeeper/eventlog/${profile}`);}
}
class snapshot {
  createSnapshot(profile) {return http.get(`/zookeeper/snapshot/${profile}`);}
}

export {
  entLiveParams,
  entLiveCDParams,
  entLiveNSSParams,
  serLiveParams,
  serLiveCDParams,
  eventLiveLog,
  snapshot
}
