<template>
  <div>
    <v-row>
      <v-col cols="12" sm="12" md="12" class="px-4">
        <h1 class="d-flex align-center justify-center"> {{this.name }} </h1>
        <div class="py-4 d-flex align-center justify-center">
          <v-btn-toggle
            v-model="selectedButton"
            shaped
            mandatory
            :color= "`${this.$store.state.selectedTemplate.color ? this.$store.state.selectedTemplate.color : 'primary'}`"
          >
            <v-btn 
              value="LIVE"
              @click="switchToLive">
              LIVE
            </v-btn>
            <v-btn
              value="SNAPSHOT"
              @click="switchToSnapshot">
              SNAPSHOT
            </v-btn>
          </v-btn-toggle>
        </div>
        <v-select
          class="pt-2 d-flex align-center justify-center"
          v-model="inputDataComp"
          item-text = "name"
          item-value = "value"
          :items="inputData"
          :label="profileSnpashotText"
          return-object
          solo
          outlined
          dense
        />
     </v-col>
   </v-row>
  </div>
</template>
<script>
import ProfilesDataService from "../services/ProfilesDataService";
import SnapshotsDataService from "../services/SnapshotsDataService";

  export default {
    name: 'Compare',

    data: () => ({
      button: "LIVE",
      selectedDataSet: "",
      inputData: [],
    }),
    props: ["name"],

    computed: {
      profileSnpashotText: {
        get () {
          return (this.button == "LIVE") ? "Select profile" : "Select snapshot"
        }
      },
      inputDataComp: {
        get () {
          return this.inputData
        },
        set (newval) {
          this.selectedDataSet = newval
          this.$emit("data-selected", newval, this.button);
          return this.inputData
        }
      },
      selectedButton: {
        get () {
          return this.button
        },
        set (newval) {
          this.button = newval
          this.selectedDataSet = ""
          this.$emit("data-clear");
          return this.button
        }
      },
    },
    mounted() {
      this.switchToLive ()
    },
    methods: {
      dataTranform (input) {
        var result = [];
        for (let parent in input) {
          input[parent].name = parent  
          result.push(input[parent]);
        }
        return result
      },
      async switchToSnapshot () {
        try {
          const response = await SnapshotsDataService.getSnapshotList(this.$store.state.selectedTemplate.name)
          this.inputData = response.data
        } catch (e) {
          console.log(e);
        }
      },
      async switchToLive () {
        try {
          const response = await ProfilesDataService.getAll()
          this.inputData = this.dataTranform(response.data)
        } catch(e) {
          console.log(e);
        }
      },
    }
  }
</script>
<style scoped>
  .v-btn { width: 125px; }
</style>
