<template>
  <div>
    <v-row class="pt-12 px-12 d-flex align-center justify-center" >
      <v-col cols="12" sm="4" md="4" >
        <CompareSelect
          @data-selected='dataSelectedFirst'
          @data-clear='dataClearFirst'
          :name="selectNameFirst"
        />
      </v-col>
      <v-col cols="12" sm="4" md="4">
        <CompareSelect
          @data-selected='dataSelectedSecond'
          @data-clear='dataClearSecond'
          :name="selectNameSecond"
        />
      </v-col>
    </v-row>
    <v-row class="d-flex justify-center my-0">
      <v-col cols="12" sm="2" md="2" class = "py-0">
        <v-select
          class="py-2"
          v-show="showServerselect"
          v-model="selectedServerFirst"
          :items="listOfServersFirst"
          label="Select server to compare 1"
          return-object
          solo
          outlined
          dense
        />
      </v-col>
      <v-col cols="12" sm="3" md="3" class="justify-center py-0">
        <v-card
          class = "px-2 py-2 align-center justify-center"
          elevation="0"
        >
        <v-select
          v-model="selectedParamtTypeComp"
          :items="paramtypes"
          item-text = "name"
          item-value = "value"
          :disabled="paramTypeDisabled"
          label="Select param type"
          return-object
          solo
          outlined
          dense
        />
          <v-btn
            block
            :loading="isLoading"
            :disabled="compareButtonDisabled"
            class = "white--text"
            :color= "`${this.$store.state.selectedTemplate.color ? this.$store.state.selectedTemplate.color + ' darken-1': 'primary'}`"
            @click="compareData"
          >
            <v-badge
            class = "px-2"
            :value="isDisabledBadge"
            content="TRIAL"
            bordered
            color="warning"
            >
              Compare
            </v-badge>
          </v-btn>
        </v-card>
      </v-col>
      <v-col cols="12" sm="2" md="2" class = "py-0">
        <v-select
          class="py-2"
          v-show="showServerselect"
          v-model="selectedServerSecond"
          :items="listOfServersSecond"
          label="Select server to compare 2"
          return-object
          solo
          outlined
          dense
        />
      </v-col>
    </v-row>
    <v-row class="d-flex justify-center">
      <v-divider class = "pt-8"/>
    </v-row>
    <v-row class="d-flex align-center justify-center py-0">
      <v-col cols="12" sm="5" md="5" class = "py-0">
        <v-card
          class = "px-4 py-0 align-center justify-center"
          :disabled="disableFilters"
          elevation="0"
        >
          <v-card-title class = "px-0 pb-0 align-center justify-center"> Search & Filter </v-card-title>
          <v-card-text class = "px-4 py-0">
            <v-text-field
              class = "py-0"
              v-model="searchComp"
              label="Search"
              flat
              hide-details
              clearable
              clear-icon="mdi-close-circle-outline"
            />
          </v-card-text>
          <v-card-actions class="align-center justify-center">
            <v-btn-toggle
              v-model="diffButtonsComp"
              multiple
            >
              <v-btn value = "same">
                <v-icon color="green">mdi-check</v-icon>
              </v-btn>
              <v-btn value = "diff">
                <v-icon color="orange">mdi-not-equal-variant</v-icon>
              </v-btn>
              <v-btn value = "plus">
                <v-icon color="red">mdi-plus</v-icon>
              </v-btn>
            </v-btn-toggle>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <v-row class="py-4">
      <v-col cols="12" sm="6" md="6">
        <CompareTree :inputTreeData='filteredDataFirst'/>
      </v-col>
      <v-col cols="12" sm="6" md="6">
        <CompareTree :inputTreeData='filteredDataSecond'/>
      </v-col>
    </v-row>

  </div>
</template>

<script>
  import CompareSelect from '../components/CompareSelect'
  import CompareTree from '../components/CompareTree'
  import SnapshotsDataService from "../services/SnapshotsDataService";

  import {
    entLiveParams,
    entLiveCDParams,
    entLiveNSSParams,
    serLiveParams,
    serLiveCDParams,
  } from "../services/ZookeeperLiveDataService";

  const entParamsDataService = new entLiveParams();
  const entCDParamsDataService = new entLiveCDParams();
  const entNSSParamsDataService = new entLiveNSSParams();
  const serParamsDataService = new serLiveParams();
  const serCDParamsDataService = new serLiveCDParams();


  export default {
    name: 'CompareView',
    components: {
      CompareSelect,
      CompareTree
    },

    data: () => ({
      selectNameFirst: "Compare...",
      selectNameSecond: "...To",
      bLoading: false,
      search: "",
      caseSensitive: "",
      diffButtons: [],
      selectedDataSetFirst: "",
      selectedDataTypeFirst: "",
      selectedDataSetSecond: "",
      selectedDataTypeSecond: "",
      listOfServersFirst: [],
      listOfServersSecond: [],
      selectedServerFirst: "",
      selectedServerSecond: "",
      dataSetFirst: {},
      dataSetSecond: {},
      showDataFirst: [],
      showDataSecond: [],
      filteredDataFirst: [],
      filteredDataSecond: [],
      selectedParamtType: {name: "", value: ""},
      paramtypes: [
        {name: "Enterprise parameters", value: "entParam"},
        {name: "Named subsystems", value: "nss"},
        {name: "Server parameters", value: "serParam"},
        {name: "Component definitions", value: "entComp"},
        {name: "Server components", value: "serComp"},
        
      ]
    }),
    computed: {
      paramTypeDisabled() {
          return (this.selectedDataSetFirst && this.selectedDataSetSecond) ? false : true
      },
      compareButtonDisabled() {
        var isServersSelected = true
        if (this.selectedParamtType.value == "serParam" ||this.selectedParamtType.value == "serComp"){
          if (!this.selectedServerFirst || !this.selectedServerFirst) {isServersSelected = false}
        }
          return (this.selectedDataSetFirst && this.selectedDataSetSecond && this.selectedParamtType.value && !this.bLoading && !this.isDisabledBadge && isServersSelected) ? false : true
      },
      showServerselect () {
        return (this.selectedParamtType.value == "serParam" || this.selectedParamtType.value == "serComp") ? true: false
      },
      disableFilters () {
        return (this.showDataFirst.length == 0 && this.showDataSecond.length == 0) ? true: false
      },
      isLoading (){
        return this.bLoading
      },
      isDisabledBadge() {
        return (this.$store.state.trial) ? true : false
      },
      searchComp: {
        get () {
          return this.search
        },
        set (newval) {
          this.search = newval
          this.filteredDataFirst = this.filter(this.showDataFirst, this.search, this.diffButtons)
          this.filteredDataSecond = this.filter(this.showDataSecond, this.search, this.diffButtons)
        }
      },

      diffButtonsComp: {
        get () {
          return this.diffButtons
        }, set (newval){
          this.diffButtons = newval
          this.filteredDataFirst = this.filter(this.showDataFirst, this.search, this.diffButtons)
          this.filteredDataSecond = this.filter(this.showDataSecond, this.search, this.diffButtons)
        }
      },
      selectedParamtTypeComp: {
        get () {
          return this.selectedParamtType
        },
        set (newval) {         
          this.selectedParamtType = newval
          this.bLoading = true
          this.listOfServersFirst   = []
          this.selectedServerFirst  = ""
          this.listOfServersSecond   = []
          this.selectedServerSecond  = ""
          this.getCompareData();
          return this.selectedParamtType
        }
      },
    },
    methods: {
      filter(array, text, options) {
        const getNodes = (result, object) => {
          if (object.name.indexOf(text) > -1) {
            if (options.length > 0){
              for (let param of options) {
                if (object.diff == param && !('children' in object)) {
                  result.push(object);
                  return result;
                }
              }
            } else {
              result.push(object);
              return result;
            }
          }
          if (Array.isArray(object.children)) {
            const children = object.children.reduce(getNodes, []);
            if (children.length) result.push({ ...object, children });
          }
          return result;
        };
        return array.reduce(getNodes, []);
      },
      dataSelectedFirst (data, type){
        this.dataClearFirst();
        this.selectedDataSetFirst = data
        this.selectedDataTypeFirst = type
      },
      dataSelectedSecond (data, type){
        this.dataClearSecond();
        this.selectedDataSetSecond = data
        this.selectedDataTypeSecond = type
      },
      dataClearFirst() {
        this.selectedDataSetFirst = ""
        this.selectedDataTypeFirst= ""
        this.listOfServersFirst   = []
        this.selectedServerFirst  = ""
        this.selectedParamtType   = {name: "", value: ""}
      },
      dataClearSecond() {
        this.selectedDataSetSecond  = ""
        this.selectedDataTypeSecond = ""
        this.listOfServersSecond    = []
        this.selectedServerSecond   = ""
        this.selectedParamtType     = {name: "", value: ""}
      },
      async getLiveData (profileName){
        var response = []
        switch (this.selectedParamtType.value) {
          case "entParam":
            response = await entParamsDataService.getAll(profileName);
            break;
          case "entComp":
            response = await entCDParamsDataService.getAll(profileName);
            break;
          case "serParam":
            response = await serParamsDataService.getAll(profileName);
            break;
          case "serComp":
            response = await serCDParamsDataService.getAll(profileName);
            break;
          case "nss":
            response = await entNSSParamsDataService.getAll(profileName);
            break;
          default:
            console.log("default", this.selectedParamtType.value)
            break;
        }
        return response.data
      },
      async readSnapshotData(snapName) {
        try {
          const response = await SnapshotsDataService.readSnapshot({"snapshotName": snapName})
          return response.data[this.selectedParamtType.value]
        } catch (e){
          console.log(e)
        }
      },
      async getCompareData() {
        try {
          if (this.selectedDataTypeFirst == "SNAPSHOT") {
            this.dataSetFirst = await this.readSnapshotData(this.selectedDataSetFirst)
          } else {
            this.dataSetFirst = await this.getLiveData(this.selectedDataSetFirst.name)
          }
          if (this.selectedDataTypeSecond == "SNAPSHOT") {
            this.dataSetSecond = await this.readSnapshotData(this.selectedDataSetSecond)
          } else {
            this.dataSetSecond = await this.getLiveData(this.selectedDataSetSecond.name)
          }
          if (this.selectedParamtType.value == "serParam" || this.selectedParamtType.value == "serComp"){
            this.listOfServersFirst = Object.keys(this.dataSetFirst)
            this.listOfServersSecond= Object.keys(this.dataSetSecond)
          }
          this.bLoading = false
        } catch (e) {
          console.log(e)
        }
      },
        
      compareData() {
        console.log("Contact us for full version")
      },


    }

  }
</script>
